@media (max-width: 768px) {
  .header {
    position: relative;
    z-index: 50;
    padding: 10px;
    background-color: #fff;
  }
  .header__burger {
    display: block;
    position: absolute;
    width: 30px;
    height: 20px;
    top: 15px;
    right: 10px;
    z-index: 50;
  }
  .header__burger span {
    position: absolute;
    background-color: #000;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    z-index: 50;
    transition: all 0.3s ease;
  }
  .header__burger:before,
  .header__burger:after {
    content: '';
    background-color: #000;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    z-index: 50;
    transition: all 0.3s ease;
  }
  .header__burger::before {
    top: 0;
  }
  .header__burger::after {
    bottom: 0;
  }
  .header__burger.active:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    bottom: 9px;
  }
  .header__burger.active span {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  .header__body {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    /* background-color: #fff; */
    position: relative;
    z-index: 10;
  }
  /* .header__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #fff;
  } */
  .header__nav {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    transition: all 0.3s ease;
  }
  .header__nav.active {
    top: 0;
  }
  .header__link {
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }

  .header__icon {
    transition: all 0.3s ease;
  }

  .header__link {
    padding: 0.8rem 1.6rem;
    border-radius: 2rem;
  }

  .header__logo--nav {
    display: -webkit-flex;
    display: flex;
    margin-bottom: 0.9rem;
  }
  body.lock {
    overflow: hidden;
  }
}

